<template>
  <v-container>
    <dialog-generic :data="dataDialog" />
    <dialog-forgotpw :data="dataDialogForgotPw" />
    <v-layout justify-center wrap>
      <v-flex xs12 sm6>
        <v-flex xs12 class="mt-2 text-center" text-xs-center>
          <h1 class="font-weight-light primary--text">HAWINSOFT</h1>
          <h2 class="font-weight">
            Web scraping
            <v-icon color="success" class="mr-1">sync_alt</v-icon>Browser remote
          </h2>
        </v-flex>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field
            prepend-icon="mail"
            name="email"
            label="Email"
            type="text"
            :rules="isEmailValid"
            v-model="form.email"
          ></v-text-field>
          <v-text-field
            prepend-icon="lock"
            name="password"
            label="Senha"
            id="password"
            type="password"
            required
            maxlength="8"
            :counter="8"
            v-model="form.password"
            :rules="isPasswordValid"
          ></v-text-field>
        </v-form>
        <v-flex xs12 mt-2>
          <v-btn
            class="font-weight-light"
            color="primary"
            small
            flat
            round
            @click="goForgotPassword()"
          >
            <v-icon small left>lock</v-icon>Esqueci minha senha
          </v-btn>
        </v-flex>
        <v-flex xs12 mt-3>
          <vue-recaptcha @verify="onVerify" @expired="onExpired" :sitekey="sitekey"></vue-recaptcha>
        </v-flex>
        <v-flex xs12 mt-4>
          <v-btn
            :disabled="!valid"
            color="success"
            block
            large
            outline
            round
            @click="validate()"
          >Entrar</v-btn>
        </v-flex>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions } from "vuex";
import router from "@/router";
import DialogGeneric from "@/components/organisms/Dialog/DialogGeneric";
import DialogForgotpw from "@/components/organisms/Dialog/DialogForgotPassword";
import VueRecaptcha from "vue-recaptcha";
import { EventBus } from "@/services/event-bus.js";
export default {
  components: {
    VueRecaptcha,
    DialogGeneric,
    DialogForgotpw
  },
  data() {
    return {
      dataDialogForgotPw: {
        size: "500",
        countInput: 0
      },
      dataDialog: {
        // success | information | error
        type: "information",
        title: "Sesso expirada!",
        textButton: "log in",
        iconButton: "keyboard_backspace",
        sessionExpired: true,
        size: "290"
      },
      showMenuPrivate: false,
      sitekey: "6LesJKQUAAAAAPuojWPcTSEYQbDBOzmQtMTS8j_g",
      valid: true,
      recaptcha: false,
      reg: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/,
      form: {
        email: "",
        password: ""
      },
      isEmailValid: [
        v => !!v || "Email  obrigatrio",
        v => this.reg.test(this.form.email) || "Email invlido"
      ],
      isPasswordValid: [
        v => !!v || "Senha  obrigatrio",
        v => v.length >= 8 || "A senha deve ter no mximo 8 caracteres"
      ]
    };
  },
  methods: {
    ...mapActions("auth", ["logIn"]),
    goForgotPassword() {
      EventBus.$emit("dialogForgotPw", true);
    },
    onVerify: function(recaptchaToken) {
      if (recaptchaToken) {
        this.recaptcha = true;
      }
    },
    onExpired: function() {
      this.$refs.recaptcha.reset();
    },
    validate() {
      if (this.$refs.form.validate()) {
        if (this.recaptcha) {
          this.logIn(this.form)
            .then(res => {
              if (res.data.auth) {
                sessionStorage.hawinsoft = res.data.auth;
                sessionStorage.hawinsoft_profile = res.data.id_user_profile;
                EventBus.$emit("showMenuPrivate", true);
                router.push({ name: "Home" });
              } else {
                this.dataDialog.type = "error";
                this.dataDialog.title = "Usurio ou senha invlido.";
                this.dataDialog.textButton = "Ok, Entendi";
                this.dataDialog.iconButton = "check";
                EventBus.$emit("dialogGeneric", true);
              }
            })
            .catch(() => {
              this.dataDialog.type = "information";
              this.dataDialog.title = "Servio temporariamente indisponvel.";
              this.dataDialog.textButton = "Ok, Entendi";
              this.dataDialog.iconButton = "check";
              EventBus.$emit("dialogGeneric", true);
            });
        } else {
          this.dataDialog.type = "error";
          this.dataDialog.title = "ReCaptcha invlido.";
          this.dataDialog.textButton = "Ok, Entendi";
          this.dataDialog.iconButton = "check";
          EventBus.$emit("dialogGeneric", true);
        }
      }
    }
  }
};
</script>
<style>
.card-bord-top {
  border-top-style: solid;
  border-top-color: #357ca5;
}
</style>